
import { Options, Vue } from 'vue-class-component'
import HeaderMenu from './Menu/index.vue'
import RouteTitle from './RouteTitle/index.vue'

@Options({
  name: 'Layout',
  components: {
    HeaderMenu,
    RouteTitle,
  },
  watch: {
    $route() {
      this.showMenu = false
    },
  },
})
export default class extends Vue {
  showMenu = false
}
